import React from "react";
import {compose} from "redux";
import {connect} from "react-redux";
import {withStyles} from "@material-ui/core/styles";
import {withTranslation} from "react-i18next";
import PropTypes from "prop-types";
import {withRouter} from 'react-router-dom';
import {Button, Tooltip} from '@material-ui/core';
import * as authActions from "./../../_actions/auth";
import PlayIcon from "./../../images/play.svg";
import CustomInlineSvg from "./CustomInlineSvg";
import PauseIcon from "./../../images/pause.svg";
const styles = {
    customPlayAudio: {
        '& .btnPlay': {
            borderRadius: 15,
            padding: '4px 8px',
            margin: '4px',
            outline: 'none',
            border: 'none',
            cursor: 'pointer',
            backgroundColor: '#113990',
            '&:focus': {
                border: 'none',
                outline: 'none',
            },
            '& svg': {
                height: 16,
                '& path': {
                    fill: '#fff!important',
                }
            },
        }
    }
};

class CustomPlayAudio extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            playAudio: false
        };
        this.audioRef = new Audio(this.props.audioUrl)
    }


    componentDidMount() {
        this.audioRef.addEventListener('ended', () => this.setState({ playAudio: false }));
    }

    componentWillUnmount() {
        this.audioRef.removeEventListener('ended', () => this.setState({ playAudio: false }));
    }

    playAudio() {

    }

    togglePlay = () => {
        this.setState({ playAudio: !this.state.playAudio }, () => {
            this.state.playAudio ? this.audioRef.play() : this.audioRef.pause();
        });
    }
    render() {
        const {
            classes,
        } = this.props;
        const {
            playAudio
        } = this.state;
        return (
            <div className={classes.customPlayAudio}>
                <Tooltip title={playAudio ? this.props.t('label.pause') : this.props.t('label.play')}>
                    <Button class="btnPlay" onClick={this.togglePlay}>
                        <CustomInlineSvg
                            svgProps={{
                                src: playAudio ? PauseIcon: PlayIcon
                            }}
                        />
                    </Button>
                </Tooltip>
            </div>
        );
    }
}

CustomPlayAudio.propTypes = {
    classes: PropTypes.object.isRequired,
};


const mapStateToProps = (state) => {
    return {
        dataLogin: state.authReducer.dataLogin,
    }
};

const mapDispatchToProps = (dispatch)=>{
    return {
        setDataLogin: (dataLogin) => dispatch(authActions.setDataLogin(dataLogin)),
        setDataUser: (dataUser) => dispatch(authActions.setDataUser(dataUser)),
    }
};

export default compose(
    connect(mapStateToProps,mapDispatchToProps),
    withStyles(styles),
    withRouter,
    withTranslation()
)(CustomPlayAudio);
