import React from "react";
import {compose} from "redux";
import {connect} from "react-redux";
import {withStyles} from "@material-ui/core/styles";
import {withTranslation} from "react-i18next";
import PropTypes from "prop-types";
import {withRouter} from 'react-router-dom';
import {Button, TextField, Tooltip} from '@material-ui/core';
import firebaseApp from "../../firebaseApp";
import AddIcon from "./../../images/add.svg";
import * as applicationActions from "./../../_actions/application";
import CustomInlineSvg from "../../theme/CustomElements/CustomInlineSvg";
import { DataGrid } from '@material-ui/data-grid';
import StoryForm from "./StoryForm";
import CustomModal from "../../theme/CustomElements/CustomModal";
import {FORM_TYPE_EDIT, FORM_TYPE_NEW} from "../../constants/constants";
import DeleteIcon from "../../images/delete.svg";
import EditIcon from "../../images/edit.svg";
import ViewIcon from "../../images/eye.svg";
import moment from "moment";
import StoryView from "./StoryView";
import DialogForm from "../../theme/CustomElements/DialogForm";
import LoadingAction from "../../theme/LoadingAction";
import Autocomplete from '@material-ui/lab/Autocomplete';
import {toast, ToastContainer} from "react-toastify";
import {MONTSERRAT, NUNITO} from "../../constants/fontCss";
import CustomPlayAudio from "../../theme/CustomElements/CustomPlayAudio";

const styles = {
    categoryWrapper: {
        width: '100%',
        height: '100vh',
        display: 'flex',
        flexDirection: 'column',
    },
    categoryHeader: {
        padding: '2rem 1rem',
        margin: '0 1rem',
        borderBottom: '1px solid #D8DDE6',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'flex-end',
        '& .selectCategory': {
            flexGrow: 1,
            fontFamily: MONTSERRAT,
        },
        '& .addCategory': {
            background: '#113990',
            borderRadius: 20,
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            padding: '1rem 2rem',
            color: '#fff',
            cursor: 'pointer',
            '& .text': {
                fontSize: '1rem',
                fontWeight: 600,
                paddingLeft: 10,
            }
        }
    },
    categoryContent: {
        flexGrow: 1,
        padding: '1rem',
        '& .cellPhoto': {
            height: 52,
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            cursor: 'pointer',
            '& img': {
                height: 50,
                width: 'auto',
            }
        },
        '& .cellAction': {
            '& .btnAction': {
                borderRadius: 15,
                padding: '4px 8px',
                margin: '4px',
                outline: 'none',
                border: 'none',
                cursor: 'pointer',
                '&:focus': {
                    border: 'none',
                    outline: 'none',
                },
                '& svg': {
                    height: 16,
                    '& path': {
                        fill: '#fff!important',
                    }
                },
                '&.view': {
                    background: '#0DBE2A',
                },
                '&.edit': {
                    background: '#FF9518',
                },
                '&.delete': {
                    background: '#E33838',
                }
            }
        },
        '& .cellDescription, .cellCategory': {
            overflow: "hidden",
            whiteSpace: "nowrap",
            textOverflow: "ellipsis"
        },
        '& .cellDate': {

        }
    },
    modalNew: {
        // minWidth: 1000,
        minWidth: '90%',
        '& .bodyModalWrapper': {
            height: 400,
            overflowY: 'auto',
        }
    },
    categoryLabel: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'flex-start',
        '& .photo': {
            height: 36,
            width: 80,
            textAlign: 'left',
            '& img': {
                height: 36,
                borderRadius: 7
            }
        },
        '& .name': {
            fontSize: '0.875rem',
            fontWeight: 500,
            color: '#001529',
            fontFamily: NUNITO,
            padding: '0.5rem 0'
        }
    }
};

class StoryPage extends React.Component {
    constructor(props) {
        super(props);
        console.log(props.categoryFilter)
        this.state = {
            openModalNew: false,
            openModalEdit: false,
            openModalView: false,
            openModalDelete: false,

            storyId: null,
            isLoading: false,

            dataCategories: [],
            categoryFilter: props.categoryFilter,
            dataStories: [],

            loadingDataGrid: false,
        };
        this.openModal = this.openModal.bind(this);
        this.closeModal = this.closeModal.bind(this);
        this.getDataStories = this.getDataStories.bind(this);
        this.changeCategoryFilter = this.changeCategoryFilter.bind(this);
    }

    componentDidMount() {
        this.getDataCategories();
    }

    getDataCategories() {
        this.getCategoriesFirebase()
            .then(res => {
                const dataCategories = [];
                res.forEach(resData => {
                    const data = resData.data();
                    let createdAt = null;
                    let updatedAt = null;
                    try {
                        createdAt = data.createdAt.toDate();
                        updatedAt = data.updatedAt.toDate();
                    } catch (e) {

                    }

                    dataCategories.push({
                        id: resData.id,
                        name: data.name,
                        description: data.description,
                        photo: data.photo,
                        createdAt: createdAt,
                        updatedAt: updatedAt,
                    })
                })
                this.props.setCategoryFilter()
                this.setState({
                    dataCategories: dataCategories,
                    categoryFilter: this.state.categoryFilter === null ? dataCategories.length ? dataCategories[0] : null : this.state.categoryFilter
                }, () => {
                    this.props.setCategoryFilter(this.state.categoryFilter);
                    this.getDataStories(this.state.categoryFilter ? this.state.categoryFilter.id : null)
                })
            })
    }

    getDataStories(categoryId) {
        this.setState({
            loadingDataGrid: true
        })
        this.getStoriesFirebase(categoryId)
            .then(res => {
                const dataStories = [];
                res.forEach(resData => {
                    const data = resData.data();
                    let createdAt = null;
                    let updatedAt = null;
                    try {
                        createdAt = data.createdAt.toDate();
                        updatedAt = data.updatedAt.toDate();
                    } catch (e) {

                    }

                    dataStories.push({
                        id: resData.id,
                        nameEn: data.nameEn,
                        nameVi: data.nameVi,
                        descriptionEn: data.descriptionEn,
                        descriptionVi: data.descriptionVi,
                        contentEn: data.contentEn,
                        contentVi: data.contentVi,
                        photo: data.photo,
                        audio: data.audio,
                        createdAt: createdAt,
                        updatedAt: updatedAt,
                    })
                })
                this.setState({
                    dataStories: dataStories,
                    loadingDataGrid: false,
                })
            })
    }

    async getCategoriesFirebase() {
        const response = firebaseApp
            .firestore()
            .collection('categories')
            .orderBy('updatedAt', 'desc')
            .get();
        return await response;
    }

    async getStoriesFirebase(categoryId) {
        const response = firebaseApp
            .firestore()
            .collection('categories')
            .doc(categoryId.toString())
            .collection('stories')
            .orderBy('updatedAt', 'desc')
            .get();
        return await response;
    }

    openModal(modalType) {
        this.setState({
            [modalType]: true
        })
    }
    closeModal(modalType) {
        this.setState({
            [modalType]: false
        })
    }
    editStory(storyId) {
        this.setState({
            storyId: storyId
        })
        this.openModal('openModalEdit');
    }
    viewStory(storyId) {
        this.setState({
            storyId: storyId
        })
        this.openModal('openModalView');
    }
    deleteStory(storyId) {
        this.setState({
            storyId: storyId
        })
        this.openModal('openModalDelete')
    }

    submitDeleteStory(categoryId, storyId) {
        this.setState({
            isLoading: true,
        })
        this.deleteFirestoreStory(categoryId,storyId)
            .then(res => {
                this.getDataStories(categoryId)
                this.setState({
                    isLoading: false,
                })
                toast.success(this.props.t('label.success'));
            })
            .catch(err => {
                this.setState({
                    isLoading: false,
                })
                toast.error("Error");
            })
    }

    async deleteFirestoreStory(categoryId,storyId) {
        const response = firebaseApp
            .firestore()
            .collection('categories')
            .doc(categoryId.toString())
            .collection('stories')
            .doc(storyId.toString())
            .delete();
        return await response;
    }

    changeCategoryFilter(value) {
        this.setState({
            categoryFilter: value
        }, () => {
            this.props.setCategoryFilter(this.state.categoryFilter);
            this.getDataStories(this.state.categoryFilter ? this.state.categoryFilter.id : null)
        })
    }

    render() {
        const {
            classes,
        } = this.props;
        const {
            openModalNew,
            openModalEdit,
            openModalView,
            openModalDelete,

            dataStories,
            storyId,
            isLoading,
            dataCategories,
            categoryFilter,
            loadingDataGrid
        } = this.state;
        const columns = [
            {
                field: 'id',
                headerName: this.props.t('story.id'),
                width: 200
            },
            {
                field: 'photo',
                headerName: this.props.t('story.photo'),
                width: 120,
                renderCell: (params) => {
                    return <div className="cellPhoto" onClick={() => this.viewStory(params.row.id)}>
                        {params.row.photo && <img src={params.row.photo} alt=""/>}
                    </div>
                }
            },
            {
                field: 'audio',
                headerName: this.props.t('story.audio'),
                width: 120,
                renderCell: (params) => {
                    return <div className="cellPhoto">
                        {params.row.audio && <CustomPlayAudio
                            audioUrl={params.row.audio}
                        />}
                    </div>
                }
            },
            {
                field: 'nameEn',
                headerName: this.props.t('story.nameEn'),
                width: 120,
                renderCell: (params) => {
                    return <div className="cellDescription">
                        {params.row.nameEn ?? ""}
                    </div>
                }
            },
            {
                field: 'descriptionEn',
                headerName: this.props.t('story.descriptionEn'),
                width: 240,
                renderCell: (params) => {
                    return <div className="cellDescription">
                        {params.row.descriptionEn ?? ""}
                    </div>
                }
            },
            {
                field: 'createdAt',
                headerName: this.props.t('story.createdAt'),
                type: "datetime",
                width: 200,
                renderCell: (params) => {
                    return <div className="cellDate">
                        {params.row.createdAt && moment(params.row.createdAt).format('llll')}
                    </div>
                }
            },
            {
                field: 'updatedAt',
                headerName: this.props.t('story.updatedAt'),
                type: "datetime",
                width: 200,
                renderCell: (params) => {
                    return <div className="cellDate">
                        {params.row.updatedAt && moment(params.row.updatedAt).format('llll')}
                    </div>
                }
            },
            {
                field: 'action',
                headerName: ' ',
                width: 180,
                renderCell: (params) => {
                    return <div className="cellAction">
                        <Tooltip
                            title={this.props.t('label.view')}
                        >
                            <Button class="btnAction view" onClick={() => this.viewStory(params.row.id)}>
                                <CustomInlineSvg
                                    svgProps={{
                                        src: ViewIcon
                                    }}
                                />
                            </Button>
                        </Tooltip>
                        <Tooltip
                            title={this.props.t('label.view')}
                        >
                            <Button class="btnAction edit" onClick={() => this.editStory(params.row.id)}>
                                <CustomInlineSvg
                                    svgProps={{
                                        src: EditIcon
                                    }}
                                />
                            </Button>
                        </Tooltip>
                        <Tooltip
                            title={this.props.t('label.view')}
                        >
                            <Button class="btnAction delete" onClick={() => this.deleteStory(params.row.id)}>
                                <CustomInlineSvg
                                    svgProps={{
                                        src: DeleteIcon
                                    }}
                                />
                            </Button>
                        </Tooltip>
                    </div>
                }
            },
        ];

        const rows = !loadingDataGrid ? dataStories.map((dataStory) => {
            return {
                id: dataStory.id,
                nameEn: dataStory.nameEn,
                nameVi: dataStory.nameVi,
                descriptionEn: dataStory.descriptionEn,
                descriptionVi: dataStory.descriptionVi,
                photo: dataStory.photo,
                audio: dataStory.audio,
                contentEn: dataStory.contentEn,
                contentVi: dataStory.contentVi,
                createdAt: dataStory.createdAt,
                updatedAt: dataStory.updatedAt
            };
        }) : [];
        return (
            <div className={classes.categoryWrapper}>
                {isLoading && <LoadingAction/>}
                <ToastContainer
                    position="top-center"
                    autoClose={5000}
                    hideProgressBar={false}
                />
                {openModalNew && categoryFilter && categoryFilter.id && <CustomModal
                    open={openModalNew}
                    handleClose={() => this.closeModal("openModalNew")}
                    title={this.props.t('story.addStory') + " -- " + (categoryFilter.name ?? "")}
                    hideSave={false}
                    hideCancel={true}
                    content={
                        <StoryForm
                            formType={FORM_TYPE_NEW}
                            handleClose={() => {
                                this.getDataStories(this.state.categoryFilter ? this.state.categoryFilter.id : null);
                                this.closeModal("openModalNew")
                            }}
                            categoryId={categoryFilter.id}
                        />
                    }
                    className={classes.modalNew}
                    hideFooter={true}
                />}
                {openModalEdit && categoryFilter && categoryFilter.id && <CustomModal
                    open={openModalEdit}
                    handleClose={() => this.closeModal("openModalEdit")}
                    title={this.props.t('story.updateStory') + " - " + storyId + " -- " + (categoryFilter.name ?? "")}
                    hideSave={false}
                    hideCancel={true}
                    content={
                        <StoryForm
                            formType={FORM_TYPE_EDIT}
                            storyId={storyId}
                            handleClose={() => {
                                this.getDataStories(this.state.categoryFilter ? this.state.categoryFilter.id : null);
                                this.closeModal("openModalEdit")
                            }}
                            categoryId={categoryFilter.id}
                        />
                    }
                    className={classes.modalNew}
                    hideFooter={true}
                />}
                {openModalView && categoryFilter && categoryFilter.id && <CustomModal
                    open={openModalView}
                    handleClose={() => this.closeModal("openModalView")}
                    title={this.props.t('story.viewStory') + " - " + storyId}
                    hideSave={false}
                    hideCancel={true}
                    content={
                        <StoryView
                            storyId={storyId}
                            categoryId={categoryFilter.id}
                            handleClose={() => {
                                this.closeModal("openModalView")
                            }}
                        />
                    }
                    className={classes.modalNew}
                    hideFooter={true}
                />}
                {openModalDelete && categoryFilter && categoryFilter.id && <DialogForm
                    styleNumber={2}
                    dialogProps={{
                        open: this.state.openModalDelete
                    }}
                    disagreeButtonProps={{
                        handleDisagree: () => {
                            this.closeModal("openModalDelete");
                        }
                    }}
                    agreeButtonProps={{
                        handleAgree: () => {
                            this.closeModal("openModalDelete");
                            this.submitDeleteStory(categoryFilter.id, storyId);
                        }
                    }}
                    messageProps={{
                        content: this.props.t('label.delete_question')
                    }}
                />}
                <div className={classes.categoryHeader}>
                    <div className="selectCategory">
                        {categoryFilter && <Autocomplete
                            defaultValue={categoryFilter}
                            id="country-select-demo"
                            style={{ width: 300 }}
                            options={dataCategories}
                            classes={{
                                option: classes.option,
                            }}
                            autoHighlight
                            getOptionLabel={(option) => option.name}
                            renderOption={(option) => (
                               <div className={classes.categoryLabel}>
                                   <div className="photo">
                                       {option.photo && <img src={option.photo} alt=""/>}
                                   </div>
                                   <div className="name">
                                       {option.name}
                                   </div>
                               </div>
                            )}
                            renderInput={(params) => (
                                <TextField
                                    {...params}
                                    label={this.props.t('story.select_category')}
                                    variant="outlined"
                                    inputProps={{
                                        ...params.inputProps,
                                        autoComplete: 'new-password', // disable autocomplete and autofill
                                    }}
                                />
                            )}
                            disableClearable
                            onChange={(event, value) => {
                                this.changeCategoryFilter(value)
                            }}
                        />}
                    </div>
                    { categoryFilter && <div className="addCategory" onClick={() => this.openModal("openModalNew")}>
                        <CustomInlineSvg
                            svgProps={{
                                src: AddIcon
                            }}
                        />
                        <div className="text">
                            {this.props.t('story.addStory')}
                        </div>
                    </div>}
                </div>
                <div className={classes.categoryContent}>
                    <DataGrid
                        rows={rows}
                        columns={columns}
                        pageSize={5}
                    />
                </div>
            </div>
        );
    }
}

StoryPage.propTypes = {
    classes: PropTypes.object.isRequired,
};


const mapStateToProps = (state) => {
    return {
        categoryFilter: state.applicationReducer.categoryFilter,
    }
};

const mapDispatchToProps = (dispatch)=>{
    return {
        setCategoryFilter: (categoryFilter) => dispatch(applicationActions.setCategoryFilter(categoryFilter)),
    }
};

export default compose(
    connect(mapStateToProps,mapDispatchToProps),
    withStyles(styles),
    withRouter,
    withTranslation()
)(StoryPage);
