class Navbar extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            anchorElMenu: null,
        };
        this.openMenu = this.openMenu.bind(this);
        this.closeMenu = this.closeMenu.bind(this);
        this.logout = this.logout.bind(this);
        this.exportJsonFile = this.exportJsonFile.bind(this);

        this.downloadJsonRef = React.createRef();
    }

    openMenu(event) {
        this.setState({
            anchorElMenu: event.currentTarget
        })
    }
    closeMenu() {
        this.setState({
            anchorElMenu: null
        })
    }

    logout() {
        firebaseApp
            .auth()
            .signOut()
            .then(res => {

            });
        this.props.setDataUser(null);
    }

    exportJsonFile() {
        this.getCategoriesFirebase()
            .then(resCat => {
                let dataJson = {};
                let indexCategory = 0;
                resCat.forEach((resCategory) => {
                    indexCategory ++;

                    const dataCategory = resCategory.data();
                    this.getStoriesFirebase(resCategory.id)
                        .then(resSto => {

                            const dataStories = {};
                            resSto.forEach(resData => {
                                const data = resData.data();
                                let createdAt = null;
                                let updatedAt = null;
                                try {
                                    createdAt = data.createdAt.toDate();
                                    updatedAt = data.updatedAt.toDate();
                                } catch (e) {

                                }

                                dataStories[resData.id] = {
                                    id: resData.id,
                                    nameEn: data.nameEn,
                                    nameVi: data.nameVi,
                                    descriptionEn: data.descriptionEn,
                                    descriptionVi: data.descriptionVi,
                                    contentEn: data.contentEn,
                                    contentVi: data.contentVi,
                                    photo: data.photo,
                                    audio: data.audio,
                                    createdAt: createdAt,
                                    updatedAt: updatedAt,
                                };
                            })
                            let createdAt = null;
                            let updatedAt = null;
                            try {
                                createdAt = dataCategory.createdAt.toDate();
                                updatedAt = dataCategory.updatedAt.toDate();
                            } catch (e) {

                            }
                            dataJson[resCategory.id] = {
                                id: resCategory.id,
                                name: dataCategory.name,
                                description: dataCategory.description,
                                photo: dataCategory.photo,
                                createdAt: createdAt,
                                updatedAt: updatedAt,
                                stories: dataStories
                            };
                            if (Object.keys(dataJson).length === resCat.size) {
                                this.downloadFile(JSON.stringify(dataJson), "truyen-song-ngu.json", "text/plain");
                            }
                        })

                })
            })
    }
    getCategoriesFirebase() {
        return firebaseApp
            .firestore()
            .collection('categories')
            .orderBy('updatedAt', 'desc')
            .get();
    }
    async getStoriesFirebase(categoryId) {
        const response = firebaseApp
            .firestore()
            .collection('categories')
            .doc(categoryId.toString())
            .collection('stories')
            .orderBy('updatedAt', 'desc')
            .get();
        return await response;
    }


     downloadFile(content, fileName, contentType) {
        const a = document.createElement("a");
        const file = new Blob([content], { type: contentType });
        a.href = URL.createObjectURL(file);
        a.download = fileName;
        a.click();
    }


    render() {
        const {
            classes,
            dataUser,
            location
        } = this.props;
        const {
            anchorElMenu,
        } = this.state;
        const listRouteCategory = [
            links.LINK_CATEGORY
        ];
        const ListRouteStory = [
            links.LINK_STORY_PAGE,
        ];
        return (
            <div className={classes.navbarWrapper}>
                <div className="title">
                    <NavLink
                        to={links.LINK_HOME}
                        className="linkHome"
                    >
                        TRUYỆN SONG NGỮ
                    </NavLink>
                </div>
                <Tooltip title={dataUser && dataUser.email ? dataUser.email : ""}>
                    <div className="userInfo" onClick={this.openMenu}>
                        <div className="text">
                            {dataUser && dataUser.email ? dataUser.email : ""}
                        </div>
                        <CustomInlineSvg
                            svgProps={{
                                src: ArrowBottom
                            }}
                        />
                    </div>
                </Tooltip>
                { anchorElMenu && <Popover
                    open={anchorElMenu}
                    anchorEl={anchorElMenu}
                    onClose={this.closeMenu}
                    anchorOrigin={{
                        vertical: 'bottom',
                        horizontal: 'center',
                    }}
                    transformOrigin={{
                        vertical: 'top',
                        horizontal: 'center',
                    }}
                >
                    <div className={classes.wrapperMenu}>
                        <div className="logout" onClick={this.logout}>
                            <CustomInlineSvg
                                svgProps={{
                                    src: LogoutIcon
                                }}
                            />
                        </div>
                    </div>
                </Popover>}
                <div className={classes.listMenu}>
                    <NavLink
                        to={links.LINK_CATEGORY}
                        className={"itemMenuLink" + (listRouteCategory.includes(location.pathname) ? " active" : "")}
                    >
                        <div className="itemMenu">
                            {this.props.t('navbar.category')}
                        </div>
                    </NavLink>
                    <NavLink
                        to={links.LINK_STORY_PAGE}
                        className={"itemMenuLink" + (ListRouteStory.includes(location.pathname) ? " active" : "")}
                    >
                        <div className="itemMenu">
                            {this.props.t('navbar.story')}
                        </div>
                    </NavLink>
                </div>
                <div className={classes.changeLanguage}>
                    <ChangeLanguage />
                </div>
                <div className={classes.exportJsonFile}>
                    <Button
                        className="btnExport"
                        onClick={this.exportJsonFile}
                    >
                        {this.props.t('label.exportJsonFile')}
                    </Button>
                </div>
                <div className={classes.appInfo}>
                    <div className="copyright">
                        &copy; tonytran_2021
                    </div>
                </div>
            </div>
        );
    }
}

Navbar.propTypes = {
    classes: PropTypes.object.isRequired,
};


const mapStateToProps = (state) => {
    return {
        dataLogin: state.authReducer.dataLogin,
        dataUser: state.authReducer.dataUser,
    }
};

const mapDispatchToProps = (dispatch)=>{
    return {
        setDataLogin: (dataLogin) => dispatch(authActions.setDataLogin(dataLogin)),
        setDataUser: (dataUser) => dispatch(authActions.setDataUser(dataUser)),
    }
};

export default compose(
    connect(mapStateToProps,mapDispatchToProps),
    withStyles(styles),
    withRouter,
    withTranslation()
)(Navbar);
