import React from "react";
import PropTypes from "prop-types";
import {withStyles} from "@material-ui/core/styles";
import {compose} from "redux";
import {withTranslation} from "react-i18next";
import Modal from "@material-ui/core/Modal";
import CustomButton from "./CustomButton";
import SaveIcon from '@material-ui/icons/Save';
import * as authActions from "../../_actions/auth";
import {connect} from "react-redux";

const styles = theme => ({
    modalContent: {
        background: '#fff',
        border: '1px solid #E0E0E0',
        borderRadius: 4,
        outline: 0,
        position: 'fixed',
        top: '50%',
        left: '50%',
        transform: 'translate(-50%,-50%)',
        animation: '$topToBottom 300ms linear'
    },
    '@keyframes topToBottom': {
        '0%': {
            opacity: 0,
            visibility: 'hidden',
            top: '45%'
        },
        '100%': {
            opacity: 1,
            visibility: 'visible',
            top: '50%'
        }
    },
    closeButton: {
        cursor: 'pointer',
        color: '#929AAE',
        fontSize: '1.7rem',
        lineHeight: 1
    },
    headerModal: {
        padding: '0.5rem 1rem',
        display: 'flex',
        alignItems: 'center',
        borderBottom: '1px solid #E0E0E0'
    },
    titleModal: {
        fontSize: '0.875rem',
        textTransform: 'initial',
        lineHeight: '19px',
        flexGrow: 1
    },
    bodyModal: {
        padding: '1rem'
    },
    footerModal: {
        padding: '0.5rem 1rem',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'flex-end',
        '& button': {
            '&:not(:first-child)': {
                marginLeft: 7
            }
        }
    },
    cancelButton: {
        background: '#fff!important',
        border: '1px solid #E0E0E0',
        color: '#46435A',
        textTransform: 'initial',
        '&:hover': {
            background: '#e4e4e4!important',
            '&::before': {
                display: 'none'
            }
        }
    }
});

class CustomModal extends React.Component {
    constructor(props) {
        super(props);
    }

    render() {
        const {
            classes,
            content,
            open,
            handleClose,
            title,
            style,
            hideFooter,
            hideCancel,
            hideSave,
            addIcon,
            handleSave,
            className,
            enableOnClose
        } = this.props;
        return (
            <div>
                <Modal
                    open={open}
                    onClose={enableOnClose ? handleClose : () => {}}
                >
                    <div
                        className={`${classes.modalContent} ${className? className :""}`}
                        style={style && typeof style === 'object' && Object.keys(style).length !== 0 ?
                            Object.assign({width: 300, maxHeight: '90%', overflowY: 'auto'}, style ? style : {})
                            :
                            {width: 300}
                        }
                    >
                        <div className={`${classes.headerModal} headerModalWrapper`}>
                            <div className={`${classes.titleModal} titleModal`}>
                                {title ? title : this.props.t('modal.defaultTitle')}
                            </div>
                            {/*{openModalLoginProduct ?  <div*/}
                            {/*    className={classes.closeButton}*/}
                            {/*    onClick={()=>this.props.setOpenModalLoginProduct(null)}*/}
                            {/*>*/}
                            {/*    &times;*/}
                            {/*</div>:*/}
                            <div
                                className={`${classes.closeButton} closeButtonHeader`}
                                onClick={handleClose}
                            >
                                &times;
                            </div>
                            {/*}*/}
                        </div>
                        <div className={`${classes.bodyModal} bodyModalWrapper`}>
                            {content ? content : <div>{this.props.t('modal.defaultContent')}</div>}
                        </div>
                        {!hideFooter &&
                        <div className={`${classes.footerModal} footerModalWrapper`}>
                            {!hideCancel &&<CustomButton
                                className={classes.cancelButton}
                                onClick={handleClose}
                                title={this.props.t('button.cancel')}
                            />}
                            {!hideSave &&
                            <CustomButton
                                title={this.props.t('button.save')}
                                className={"btnSaveModal"}
                                buttonProps={{
                                    startIcon: <React.Fragment><SaveIcon/></React.Fragment>
                                }}
                                onClick={handleSave}
                            />
                            }
                            {addIcon && addIcon.map((item, index) => {
                                return (
                                    <CustomButton
                                        key={index}
                                        title={item.title ? item.title : ''}
                                        buttonProps={item.buttonProps ? item.buttonProps : {}}
                                        onClick={item.handleFunction ? item.handleFunction : () => {}}
                                        className={item.className ? item.className : ''}
                                    />
                                )
                            })}
                        </div>
                        }
                    </div>
                </Modal>
            </div>
        )
    }
}

CustomModal.defaultProps = {
    handleClose: () => {},
    hideFooter: false,
    hideSave: true,
    hideXClose: false,
    hideCancle: false,
    handleSave: () => {}
}

CustomModal.propTypes = {
    classes: PropTypes.object.isRequired,
    title: PropTypes.string,
    content: PropTypes.node.isRequired,
    open: PropTypes.bool.isRequired,
    handleClose: PropTypes.func.isRequired,
    style: PropTypes.object,
    hideFooter: PropTypes.bool,
    hideXClose: PropTypes.bool,
    hideSave: PropTypes.bool,
    addIcon: PropTypes.array,
    handleSave: PropTypes.func,
    enableOnClose: PropTypes.bool,
    hideCancel: PropTypes.bool
}

const mapStateToProps = (state) => {
    return {
        // openModalLoginProduct: state.authReducer.openModalLoginProduct,
    }
};

const mapDispatchToProps = (dispatch) => {
    return {
        // setOpenModalLoginProduct: (value) => dispatch(authActions.setOpenModalLoginProduct(value)),
    }
};
export default  compose(connect(mapStateToProps,mapDispatchToProps),
    withStyles(styles),
    withTranslation()
)(CustomModal);