import React from "react";
import {compose} from "redux";
import {connect} from "react-redux";
import {withStyles} from "@material-ui/core/styles";
import {withTranslation} from "react-i18next";
import PropTypes from "prop-types";
import {withRouter} from 'react-router-dom';
import firebaseApp from "../../firebaseApp";
import * as authActions from "./../../_actions/auth";
import LoadingAction from "../../theme/LoadingAction";
import {NUNITO} from "../../constants/fontCss";
import moment from "moment";
import CustomPlayAudio from "../../theme/CustomElements/CustomPlayAudio";
const styles = {
    categoryViewWrapper: {
        color: '#001529',
        '& .photoView': {
            height: 200,
            '& img': {
                height: 200,
            }
        },
        '& .audioView': {
            padding: '0.5rem 0'
        },
        '& .nameView': {
            fontSize: '1rem',
            fontWeight: 700,
            color: '#001529',
            fontFamily: NUNITO,
            padding: '0.5rem 0'
        },
        '& .descriptionView': {
            fontSize: '0.875rem',
            fontWeight: 'normal',
            color: '#001529',
            fontFamily: NUNITO,
            padding: '0.5rem 0',
            whiteSpace: "pre-wrap",
            maxHeight: 150,
            overflowY: 'auto',
        },
        '& .dateView': {
            display: 'flex',
            alignItems: 'flex-start',
            justifyContent: 'center',
            padding: '1rem 0',
            '& .itemDate': {
                flexGrow: 1,
                padding: '0.5rem 1rem',
                '& .label': {
                    fontSize: '1rem',
                    fontWeight: 700,
                },
                '& .view': {
                    fontSize: '0.875rem',
                    fontWeight: 500,
                    whiteSpace: "pre-wrap",
                }
            },
        },
    }
};

class StoryView extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            id: null,
            nameEn: '',
            nameVi: '',
            descriptionEn: '',
            descriptionVi: '',
            photo: null,
            audio: null,
            contentEn: '',
            contentVi: '',
            createdAt: null,
            updatedAt: null,

            isLoading: false,
        };
    }

    componentDidMount() {
        const {
            storyId,
            categoryId
        } = this.props;
        this.setState({
            isLoading: true
        })
        this.showStoryFirebase(categoryId, storyId)
            .then(res => {
                const data = res.data();
                const id = res.id;
                let createdAt = null;
                let updatedAt = null;
                try {
                    createdAt = data.createdAt.toDate();
                    updatedAt = data.updatedAt.toDate();
                } catch (e) {

                }
                this.setState({
                    id: id,
                    nameEn: data.nameEn ?? "",
                    nameVi: data.nameVi ?? "",
                    descriptionEn: data.descriptionEn ?? "",
                    descriptionVi: data.descriptionVi ?? "",
                    photo: data.photo,
                    audio: data.audio,
                    contentEn: data.contentEn,
                    contentVi: data.contentVi,
                    categoryId: data.categoryId,
                    createdAt: createdAt,
                    updatedAt: updatedAt,
                    isLoading: false
                })
            })
            .catch(err => {
                this.setState({
                    isLoading: false
                })
            });
    }
    async showStoryFirebase(categoryId, storyId) {
        const response = firebaseApp
            .firestore()
            .collection('categories')
            .doc(categoryId.toString())
            .collection('stories')
            .doc(storyId.toString())
            .get();
        return await response;
    }

    render() {
        const {
            classes,
        } = this.props;
        const {
            nameEn,
            nameVi,
            descriptionEn,
            descriptionVi,
            photo,
            audio,
            contentEn,
            contentVi,
            createdAt,
            updatedAt,
            isLoading,
        } = this.state;
        return (
            <div className={classes.categoryViewWrapper}>
                {isLoading && <LoadingAction/>}
                <div className="photoView">
                    {photo && <img src={photo} alt=""/>}
                </div>
                <div className="audioView">
                    {audio && <CustomPlayAudio
                        audioUrl={audio}
                    />}
                </div>
                <div className="dateView">
                    <div className="itemDate">
                        <div className="label">
                            {this.props.t('story.nameEn')}
                        </div>
                        <div className="view">
                            {nameEn}
                        </div>
                    </div>
                    <div className="itemDate">
                        <div className="label">
                            {this.props.t('story.nameVi')}
                        </div>
                        <div className="view">
                            {nameVi}
                        </div>
                    </div>
                </div>
                <div className="dateView">
                    <div className="itemDate">
                        <div className="label">
                            {this.props.t('story.descriptionEn')}
                        </div>
                        <div className="view">
                            {descriptionEn}
                        </div>
                    </div>
                    <div className="itemDate">
                        <div className="label">
                            {this.props.t('story.descriptionVi')}
                        </div>
                        <div className="view">
                            {descriptionVi}
                        </div>
                    </div>
                </div>
                <div className="dateView">
                    <div className="itemDate">
                        <div className="label">
                            {this.props.t('story.contentEn')}
                        </div>
                        <div className="view">
                            {contentEn}
                        </div>
                    </div>
                    <div className="itemDate">
                        <div className="label">
                            {this.props.t('story.contentVi')}
                        </div>
                        <div className="view">
                            {contentVi}
                        </div>
                    </div>
                </div>
                <div className="dateView">
                    <div className="itemDate">
                        <div className="label">
                            {this.props.t('story.createdAt')}
                        </div>
                        <div className="view">
                            {createdAt && moment(createdAt).format('llll')}
                        </div>
                    </div>
                    <div className="itemDate">
                        <div className="label">
                            {this.props.t('story.updatedAt')}
                        </div>
                        <div className="view">
                            {updatedAt && moment(updatedAt).format('llll')}
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

StoryView.propTypes = {
    classes: PropTypes.object.isRequired,
};


const mapStateToProps = (state) => {
    return {

    }
};

const mapDispatchToProps = (dispatch)=>{
    return {

    }
};

export default compose(
    connect(mapStateToProps,mapDispatchToProps),
    withStyles(styles),
    withRouter,
    withTranslation()
)(StoryView);
