import {MONTSERRAT} from "../../constants/fontCss";
import SaveIcon from "@material-ui/icons/Save";
import CustomButton from "../../theme/CustomElements/CustomButton";
import LoadingAction from "../../theme/LoadingAction";
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
const styles = {
    categoryFormWrapper: {

    },
    formItem: {
        borderBottom: '1px solid #D8DDE6',
        padding: '0.5rem',
        '& .label': {
            fontSize: '0.875rem',
            color: '#001034',
            fontWeight: 700,
        },
        '& .input': {
            '& .inputMui': {
                backgroundColor: '#F5F6F9',
                width: '100%',
                borderRadius: 6,
                padding: '0.5rem 1rem',
                '& input, textarea': {
                    color: '#001034',
                    fontSize: '0.875rem',
                }
            }
        },
        '&.dropZonePhoto': {
            display: 'flex',
            alignItems: 'center',
            flexDirection: 'column',
            justifyContent: 'center',
            overflow: 'hidden',
            '&:hover': {
                '& .removeAvatar': {
                    display: "block"
                }
            },
            '& section':{
                height: 200,
                width: '100%',
                display: 'flex',
                marginBottom: 10
            },
            '& .dropPicture': {
                textAlign: 'center',
                cursor: 'pointer',
                width: 75,
                height: 75,
                margin: 'auto auto 20px',
                '&:focus':{
                    outline: 'none'
                }
            },
            '& .uploadAvatar': {
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                padding: '0.5rem 2rem',
                background: '#D8DDE6',
                borderRadius: 6,
                cursor: 'pointer',
                '& .text': {
                    color: '#001034',
                    fontSize: '0.875rem',
                    paddingLeft: 10,
                    fontFamily: MONTSERRAT
                }
            },
            '& .avatar': {
                height: '100%',
                width: '100%',
                objectFit: 'cover',
                cursor: 'pointer',
                '&.photoImage': {
                    height: 200,
                    width: 'auto',
                }
            },
            '& .removeAvatar': {
                display: "none",
                background: '#efefef',
                height: 25,
                marginTop: -25,
                padding: '0 0.5rem',
                fontWeight: 900,
                cursor: 'pointer',
                fontSize: '1rem',
                color: '#ed1313',
                textAlign: 'center',
                width: '100%',
                position: 'relative',
                '&:hover': {
                    background: '#929aae',
                },
            },
            '&.active': {
                '& .dropPicture':{
                    width: '100%',
                    height: '100%',
                },
            },
        },

    },
    formButton: {
        padding: '1rem 0',
        textAlign: 'center',
    },

};

class CategoryForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {

            id: null,
            name: '',
            description: '',
            photo: null,
            createdAt: null,
            updatedAt: null,

            isLoading: false,
        };
        this.handleChange = this.handleChange.bind(this);
        this.handleSave = this.handleSave.bind(this);
    }

    componentDidMount() {
        const {
            formType,
            categoryId
        } = this.props;
        if (formType === FORM_TYPE_EDIT) {
            this.setState({
                isLoading: true
            })
            this.showCategory(categoryId)
                .then(res => {
                    const data = res.data();
                    const id = res.id;
                    let createdAt = null;
                    let updatedAt = null;
                    try {
                        createdAt = data.createdAt.toDate();
                        updatedAt = data.updatedAt.toDate();
                    } catch (e) {

                    }
                    this.setState({
                        id: id,
                        name: data.name,
                        description: data.description,
                        photo: data.photo,
                        createdAt: createdAt,
                        updatedAt: updatedAt,
                        isLoading: false
                    })
                })
                .catch(err => {
                    this.setState({
                        isLoading: false
                    })
                });
        }
    }
    async showCategory(categoryId) {
        const response = firebaseApp
            .firestore()
            .collection('categories')
            .doc(categoryId)
            .get();
        return await response;
    }
    async addCategory(categoryId, dataCategory) {
        const response = firebaseApp
            .firestore()
            .collection('categories')
            .doc(categoryId.toString())
            .set(dataCategory);
        return await response;
    }
    async updateCategory(categoryId, dataCategory) {
        const response = firebaseApp
            .firestore()
            .collection('categories')
            .doc(categoryId.toString())
            .update(dataCategory);
        return await response;
    }
    handleChange(name, value) {
        this.setState({
            [name]: value
        })
    }

    handleSave() {
        const {
            id,
            photo,
        } = this.state;
        const {
            formType
        } = this.props;
        this.setState({
            isLoading: true
        })
        const categoryId = formType === FORM_TYPE_NEW ? new Date().getTime() : id;
        if (typeof photo === STRING || !photo) {
            this.saveData(categoryId);
        } else {
            this.uploadPhoto(categoryId, photo)
                .then(res => {
                    this.getPhotoUrl(categoryId)
                        .then(photoUrl => {
                            this.setState({
                                photo: photoUrl,
                            }, () => {
                                this.saveData(categoryId);
                            })
                        })
                        .catch(err => {
                            this.setState({
                                isLoading: false
                            })
                            toast.error("Error");
                        })

                })
                .catch(err => {
                    this.setState({
                        isLoading: false
                    })
                    toast.error("Error");
                });
        }
    }
    saveData(categoryId) {
        const {
            id,
            name,
            description,
            photo,
            createdAt,
            updatedAt,
        } = this.state;
        const {
            formType,
        } = this.props;
        const dataCategory = {
            name: name,
            description: description,
            photo: photo,
            createdAt: createdAt ?? new Date(),
            updatedAt: new Date()
        }
        if (formType === FORM_TYPE_NEW) {
            this.addCategory(categoryId, dataCategory)
                .then(res => {
                    this.props.handleClose();
                    this.setState({
                        isLoading: false
                    })
                    toast.success(this.props.t('label.success'));
                })
                .catch(err => {
                    this.setState({
                        isLoading: false
                    })
                    toast.error(this.props.t('label.error'));
                })
        } else {
            this.updateCategory(categoryId, dataCategory)
                .then(res => {
                    this.props.handleClose();
                    this.setState({
                        isLoading: false
                    })
                    if (!this.state.photo) {
                        this.deletePhoto(categoryId).then(r => {
                            //
                        });
                    }
                    toast.success(this.props.t('label.success'));
                })
                .catch(err => {
                    this.setState({
                        isLoading: false
                    })
                    toast.error(this.props.t('label.error'));
                })
        }
    }

    uploadPhoto(categoryId, photo) {
        const categoryRef = firebaseApp
            .storage()
            .ref('photo/categories/'+categoryId+".jpg");
        const response = categoryRef
            .put(photo);
        return response;
    }
    deletePhoto(categoryId) {
        const response = firebaseApp
            .storage()
            .ref('photo/categories/'+categoryId+".jpg")
            .delete();
        return response;
    }
    getPhotoUrl(categoryId) {
        const categoryRef = firebaseApp
            .storage()
            .ref('photo/categories/'+categoryId+".jpg");
        return categoryRef.getDownloadURL();
    }


    render() {
        const {
            classes,
        } = this.props;
        const {
            name,
            description,
            photo,
            isLoading,
        } = this.state;
        return (
            <div className={classes.categoryFormWrapper}>
                {isLoading && <LoadingAction/>}
                <ToastContainer />
                <div className={classes.formItem + " dropZonePhoto" + (photo ? " active" : "")}>
                    <Dropzone
                        ref={(fileUpload) => {
                            this.fileUpload = fileUpload;
                        }}
                        accept={[".jpeg", ".jpg", ".png", ".svg"]}
                        minSize={0}
                        multiple={0}
                        onDrop={acceptedFiles => this.handleChange("photo", acceptedFiles[0])}>
                        {({getRootProps, getInputProps}) => {
                            const imgAvatarIcon = photo ? typeof photo === STRING ? photo : URL.createObjectURL(photo) : PhotoIcon
                            return (<section>
                                <div {...getRootProps()} className={"dropPicture"}>
                                    <input {...getInputProps()} />
                                    <img src={imgAvatarIcon} alt={""} className={"avatar" + (photo ? " photoImage": "")}/>
                                </div>
                            </section>)
                        }}
                    </Dropzone>
                    {photo &&
                    <div className={"removeAvatar"}
                         onClick={() => this.handleChange( "photo", null)}
                    >
                        x
                    </div>
                    }
                    <div className={"uploadAvatar"} onClick={(e) => this.fileUpload.open()}>
                        <CustomInlineSvg
                            svgProps={{
                                src: CameraIcon
                            }}
                        />
                        <div className="text">
                            {this.props.t('label.upload_photo')}
                        </div>
                    </div>
                </div>
                <div className={classes.formItem}>
                    <div className="label">
                        {this.props.t('category.name')}:
                    </div>
                    <div className="input">
                        <Input
                            className="inputMui"
                            name="name"
                            value={name}
                            onChange={(event) => this.handleChange("name", event.target.value)}
                        />
                    </div>
                </div>
                <div className={classes.formItem}>
                    <div className="label">
                        {this.props.t('category.description')}:
                    </div>
                    <div className="input">
                        <Input
                            className="inputMui"
                            multiline
                            rows={4}
                            name="description"
                            value={description}
                            onChange={(event) => this.handleChange("description", event.target.value)}
                        />
                    </div>
                </div>
                <div className={classes.formButton}>
                    <CustomButton
                        title={this.props.t('button.save')}
                        className={"btnSaveModal"}
                        buttonProps={{
                            startIcon: <React.Fragment><SaveIcon/></React.Fragment>
                        }}
                        onClick={this.handleSave}
                    />
                </div>
            </div>
        );
    }
}

CategoryForm.propTypes = {
    classes: PropTypes.object.isRequired,
};


const mapStateToProps = (state) => {
    return {
        dataLogin: state.authReducer.dataLogin,
    }
};

const mapDispatchToProps = (dispatch)=>{
    return {
        setDataLogin: (dataLogin) => dispatch(authActions.setDataLogin(dataLogin)),
        setDataUser: (dataUser) => dispatch(authActions.setDataUser(dataUser)),
    }
};

export default compose(
    connect(mapStateToProps,mapDispatchToProps),
    withStyles(styles),
    withRouter,
    withTranslation()
)(CategoryForm);
