import i18n from "i18next";
import LanguageDetector from "i18next-browser-languagedetector";
// import Http from "i18next-http-backend";
import BackendAdapter from 'i18next-multiload-backend-adapter';
import translations_en from "./translations/en/translates";
import translations_vi from "./translations/vi/translates";
import Cookies from 'js-cookie';
import moment from "moment";
import { initReactI18next } from 'react-i18next'
import {LOCAL_LANGUAGE} from "./constants/constants";
export const LANGUAGE_VI = "vi";

let cookieLanguage = Cookies.get(LOCAL_LANGUAGE) ?Cookies.get(LOCAL_LANGUAGE) : Cookies.get("i18nextLng") &&  ["en","vi"].includes(Cookies.get("i18nextLng")) ? Cookies.get("i18nextLng") : LANGUAGE_VI;

moment.locale(cookieLanguage);
i18n
    .use(LanguageDetector)
    .use(initReactI18next)
    .use(BackendAdapter)
    .init({
        // debug: false,
        // // supportedLngs: ["en", "vi"],
        // lng: cookieLanguage,
        // fallbackLng: cookieLanguage,
        // interpolation: {
        //     escapeValue: false // not needed for react!!
        // },
        // // preload: ['en', 'vi'],
        // backend: {
        //     backend: Http,
        //     backendOption:{
        //         loadPath: `/api/translationmanager/list?langLocale={{lng}}`,
        //         // stringify: JSON.stringify,
        //         allowMultiLoading: true,
        //         // init option for fetch, for example
        //         requestOptions: {
        //             mode: 'cors',
        //             credentials: 'same-origin',
        //             cache: 'default',
        //         },
        //     }
        // },
        // load: 'languageOnly',
        // react: {
        //     wait: true,
        //     useSuspense: false
        // },


        supportedLngs:["en","vi"],
        lng:cookieLanguage,
        fallbackLng: "en",
        debug: false,

        ns:["translations"],
        defaultNS: "translations",
        interpolation: {
            escapeValue: false // not needed for react!!
        },
        resources: {
            en: {
                translations: translations_en,
            },
            vi: {
                translations: translations_vi,
            }
        },
        react: {
            wait: false,
            useSuspense: false
        }
    }).then(r =>{
});

export default i18n;
