import React, {Suspense} from 'react';
import {Redirect, Route, Switch, withRouter} from 'react-router-dom';
import PropTypes from "prop-types";
import {createMuiTheme, MuiThemeProvider, responsiveFontSizes, withStyles} from "@material-ui/core/styles";
import {compose} from "redux";
import {connect} from "react-redux";
import {CssBaseline} from "@material-ui/core";
import {withTranslation} from "react-i18next";
import ErrorBoundary from "./theme/PageError/ErrorBoundary";
import LoadingAction from "./theme/LoadingAction";
import Login from "./components/login/Login";
import AppMuiPickersUtilsProvider from "./theme/form/DatePicker/AppMuiPickersUtilsProvider";
import {enUS, viVN} from "@material-ui/core/locale";
import firebaseApp from "./firebaseApp";
import * as authActions from "./_actions/auth";
import * as links from "./constants/links";
import PublicRoute from "./PublicRoute";
import PrivateRoute from "./PrivateRoute";
import Home from "./components/home/Home";
import Page404 from "./theme/PageError/Page404";
import CategoryPage from "./components/category/CategoryPage";
import StoryPage from "./components/story/StoryPage";
import StoryForm from "./components/story/StoryForm";
import Navbar from "./components/Navbar";
import Page500 from "./theme/PageError/Page500";

const styles = {
  wrapper: {
    display: 'flex',
    alignItems: 'start',
    justifyContent: 'flex-start'
  }
};

class App extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      isLoading: false
    };
  }

  componentDidMount() {
    const that = this;
    firebaseApp.auth().onAuthStateChanged((user) => {
      that.setState({
        isLoading: false
      })
      if (user) {
        that.props.setDataUser(user);
      } else {
        that.props.setDataUser(null);
      }

    });
  }

  render() {
    const {
      classes,
      tReady,
      location
    } = this.props;

    const {
      isLoading
    } = this.state;
    let theme = createMuiTheme({
      palette: {
        primary: {
          main: '#47425a',
        },
        secondary:{
          main: '#645d7b',
        }
      },
      typography: {
        fontFamily: 'Open Sans , sans-serif',
      },
      overrides: {
        MuiCssBaseline: {
          '@global': {
            html: {
              WebkitFontSmoothing: 'auto',
              fontSize: 16
            },
            body: {
              fontFamily: 'Open Sans, sans-serif',
              "& .appWSPreline":{
                whiteSpace: "pre-line"
              }
            }
          },
        },
        MuiButton: {
          contained: {
            fontFamily: 'Open Sans, sans-serif',
            fontWeight: 400,
            fontStyle: 'normal',
            lineHeight: '19px',
            boxShadow: 'none!important',
            borderRadius: 10
          },
          containedSizeSmall: {
            fontSize: '0.875rem',
            padding: '0.6rem 1.4rem',
          },
          containedSizeLarge: {
            fontSize: '1.1rem',
            padding: '0.85rem 1.65rem',
          }
        }
      },
      props: {
        MuiButtonBase: {
          disableRipple: true
        },
        MuiInput: {
          disableUnderline: true
        }
      }
    }, this.props.i18n.language==="vi"?viVN:enUS);

    theme = responsiveFontSizes(theme);
    const listPrivateRoute = [
      links.LINK_CATEGORY,
      links.LINK_STORY_PAGE,
      links.LINK_HOME,
    ];
    const listPublicRoute = [
      links.LINK_LOGIN
    ];
    const listErrorRoute = [
      links.PAGE500,
      links.PAGE404
    ];
    return (
        <ErrorBoundary>
          <MuiThemeProvider theme={theme}>
            <CssBaseline />
            <AppMuiPickersUtilsProvider>
              {
                !!tReady && !isLoading ?<Suspense fallback={<LoadingAction/>}>
                  <Switch>
                    <div className={classes.wrapper}>
                      {
                        listPrivateRoute.includes(location.pathname) &&
                            <Navbar />
                      }
                      <PublicRoute path={links.LINK_LOGIN} component={() => <Login />} exact />
                      <PrivateRoute path={links.LINK_CATEGORY} component={() => <CategoryPage />} exact />
                      <PrivateRoute path={links.LINK_STORY_PAGE} component={() => <StoryPage />} exact />
                      <PrivateRoute path={links.LINK_HOME} component={() => <Home />} exact />
                      <Route path={links.PAGE500} component={Page500} exact />
                      <Route path={links.PAGE404} component={Page404} exact />
                      {!(listPrivateRoute.includes(location.pathname) || listPublicRoute.includes(location.pathname) || listErrorRoute.includes(location.pathname)) && <Redirect to={links.PAGE404}/>}
                    </div>
                  </Switch>
                </Suspense>:<LoadingAction/>
              }
            </AppMuiPickersUtilsProvider>
          </MuiThemeProvider>
        </ErrorBoundary>
    );
  }
}

App.propTypes = {
  classes: PropTypes.object.isRequired,
};

const mapStateToProps = (state) => {
  return {
    dataLogin: state.authReducer.dataLogin,
  }
};

const mapDispatchToProps = (dispatch)=>{
  return {
    setDataLogin: (dataLogin) => dispatch(authActions.setDataLogin(dataLogin)),
    setDataUser: (dataUser) => dispatch(authActions.setDataUser(dataUser)),
  }
};

export default compose(
    connect(mapStateToProps, mapDispatchToProps),
    withStyles(styles),
    withRouter,
    withTranslation()
)(App);
