import * as types from "./../_constants/application";
const initState = {
    categoryFilter: null
};
const authReducer = (state = initState, action) => {
    switch (action.type) {
        case types.APPLICATION_CATEGORY_FILTER:
            return {
                ...state,
                categoryFilter: action.categoryFilter,
            };
        default:
            return state;
    }
};

export default authReducer;
