import * as types from "./../_constants/auth";
import {DATA_LOGIN, DATA_USER} from "../constants/constants";
let dataUser = null;
let dataLogin = null;

if (localStorage.getItem(DATA_USER) !== null) {
    dataUser = JSON.parse(localStorage.getItem(DATA_USER));
}
if (localStorage.getItem(DATA_LOGIN) !== null) {
    dataLogin = JSON.parse(localStorage.getItem(DATA_LOGIN));
}
const initState = {
    dataUser: dataUser,
    dataLogin: dataLogin,
};


const authReducer = (state = initState, action) => {
    switch (action.type) {
        case types.AUTH_DATA_USER:
            if (action.dataUser !== null) {
                localStorage.setItem(DATA_USER, JSON.stringify(action.dataUser));
            } else {
                localStorage.removeItem(DATA_USER);
            }
            return {
                ...state,
                dataUser: action.dataUser,
            };
        case types.AUTH_DATA_LOGIN:
            if (action.dataLogin !== null) {
                localStorage.setItem(DATA_LOGIN, JSON.stringify(action.dataLogin));
            } else {
                localStorage.removeItem(DATA_LOGIN);
            }
            return {
                ...state,
                dataLogin: action.dataLogin,
            };
        default:
            return state;
    }
};

export default authReducer;
