import React from "react";
import {compose} from "redux";
import {connect} from "react-redux";
import {withStyles} from "@material-ui/core/styles";
import {withTranslation} from "react-i18next";
import PropTypes from "prop-types";
import {withRouter} from 'react-router-dom';
import {Button, Tooltip} from '@material-ui/core';
import firebaseApp from "../../firebaseApp";
import AddIcon from "./../../images/add.svg";
import * as authActions from "./../../_actions/auth";
import CustomInlineSvg from "../../theme/CustomElements/CustomInlineSvg";
import {DataGrid} from '@material-ui/data-grid';
import CategoryForm from "./CategoryForm";
import CustomModal from "../../theme/CustomElements/CustomModal";
import {FORM_TYPE_EDIT, FORM_TYPE_NEW} from "../../constants/constants";
import DeleteIcon from "../../images/delete.svg";
import EditIcon from "../../images/edit.svg";
import ViewIcon from "../../images/eye.svg";
import moment from "moment";
import CategoryView from "./CategoryView";
import DialogForm from "../../theme/CustomElements/DialogForm";
import LoadingAction from "../../theme/LoadingAction";
import {toast, ToastContainer} from "react-toastify";
import {MONTSERRAT} from "../../constants/fontCss";

const styles = {
    categoryWrapper: {
        width: '100%',
        height: '100vh',
        display: 'flex',
        flexDirection: 'column',
    },
    categoryHeader: {
        padding: '2rem 1rem',
        margin: '0 1rem',
        borderBottom: '1px solid #D8DDE6',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'flex-end',
        '& .addCategory': {
            background: '#113990',
            borderRadius: 20,
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            padding: '1rem 2rem',
            color: '#fff',
            cursor: 'pointer',
            '& .text': {
                fontSize: '1rem',
                fontWeight: 600,
                paddingLeft: 10,
            }
        }
    },
    categoryContent: {
        flexGrow: 1,
        padding: '1rem',
        '& .cellPhoto': {
            height: 52,
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            cursor: 'pointer',
            '& img': {
                height: 50,
                width: 'auto',
            }
        },
        '& .cellAction': {
            '& .btnAction': {
                borderRadius: 15,
                padding: '4px 8px',
                margin: '4px',
                outline: 'none',
                border: 'none',
                cursor: 'pointer',
                '&:focus': {
                    border: 'none',
                    outline: 'none',
                },
                '& svg': {
                    height: 16,
                    '& path': {
                        fill: '#fff!important',
                    }
                },
                '&.view': {
                    background: '#0DBE2A',
                },
                '&.edit': {
                    background: '#FF9518',
                },
                '&.delete': {
                    background: '#E33838',
                }
            }
        },
        '& .cellDescription': {
            overflow: "hidden",
            whiteSpace: "nowrap",
            textOverflow: "ellipsis",
            fontFamily: MONTSERRAT,
        },
        '& .cellDate': {

        }
    },
    modalNew: {
        minWidth: 600,
        width: 800,
    }
};

class CategoryPage extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            openModalNew: false,
            openModalEdit: false,
            openModalView: false,
            openModalDelete: false,

            dataCategories: [],
            categoryId: null,
            isLoading: false,
        };
        this.openModal = this.openModal.bind(this);
        this.closeModal = this.closeModal.bind(this);
    }

    componentDidMount() {
        this.getDataCategories();
    }
    getDataCategories() {
        this.getCategoriesFirebase()
            .then(res => {
                const dataCategories = [];
                res.forEach(resData => {
                    const data = resData.data();
                    let createdAt = null;
                    let updatedAt = null;
                    try {
                        createdAt = data.createdAt.toDate();
                        updatedAt = data.updatedAt.toDate();
                    } catch (e) {

                    }
                    dataCategories.push({
                        id: resData.id,
                        name: data.name,
                        description: data.description,
                        photo: data.photo,
                        createdAt: createdAt,
                        updatedAt: updatedAt,
                    })
                })
                this.setState({
                    dataCategories: dataCategories
                })
            })
    }
    getCategoriesFirebase() {
        return firebaseApp
            .firestore()
            .collection('categories')
            .orderBy('updatedAt', 'desc')
            .get();
    }
    openModal(modalType) {
        this.setState({
            [modalType]: true
        })
    }
    closeModal(modalType) {
        this.setState({
            [modalType]: false
        })
    }
    editCategory(categoryId) {
        this.setState({
            categoryId: categoryId
        })
        this.openModal('openModalEdit');
    }
    viewCategory(categoryId) {
        this.setState({
            categoryId: categoryId
        })
        this.openModal('openModalView');
    }
    deleteCategory(categoryId) {
        this.setState({
            categoryId: categoryId
        })
        this.openModal('openModalDelete')
    }
    submitDeleteCategory(categoryId) {
        // check Delete
        this.getStoriesFirebase(categoryId)
            .then(res => {
                if (res.size === 0) {
                    this.setState({
                        isLoading: true,
                    })
                    this.deleteCategoryFirebase(categoryId)
                        .then(res => {
                            this.getDataCategories();
                            this.setState({
                                isLoading: false,
                            })
                            this.deletePhotoFirebase(categoryId).then(r => {

                            });
                            toast.success(this.props.t('label.success'));
                        })
                        .catch(err => {
                            this.setState({
                                isLoading: false,
                            })
                            toast.error(this.props.t('label.error'));
                        })
                } else {
                    toast.warning(this.props.t('category.notDelete'));
                }
            })
    }
    deleteCategoryFirebase(categoryId) {
        return firebaseApp
            .firestore()
            .collection('categories')
            .doc(categoryId.toString())
            .delete();
    }
    deletePhotoFirebase(categoryId) {
        return firebaseApp
            .storage()
            .ref('photo/categories/' + categoryId + ".jpg")
            .delete();
    }
    getStoriesFirebase(categoryId) {
        return firebaseApp
            .firestore()
            .collection('categories')
            .doc(categoryId)
            .collection('stories')
            .get();
    }


    render() {
        const {
            classes,
          } = this.props;
        const {
            openModalNew,
            openModalEdit,
            openModalView,
            openModalDelete,
            dataCategories,
            categoryId,
            isLoading
        } = this.state;
        const columns = [
            {
                field: 'id',
                headerName: this.props.t('category.id'),
                width: 200
            },
            {
                field: 'photo',
                headerName: this.props.t('category.photo'),
                width: 120,
                renderCell: (params) => {
                    return <div className="cellPhoto" onClick={() => this.viewCategory(params.row.id)}>
                        {params.row.photo && <img src={params.row.photo} alt=""/>}
                    </div>
                }
            },
            {
                field: 'name',
                headerName: this.props.t('category.name'),
                width: 150
            },
            {
                field: 'description',
                headerName: this.props.t('category.description'),
                width: 240,
                renderCell: (params) => {
                    return <div className="cellDescription">
                        {params.row.description ?? ""}
                    </div>
                }
            },
            {
                field: 'createdAt',
                headerName: this.props.t('category.createdAt'),
                type: "datetime",
                width: 200,
                renderCell: (params) => {
                    return <div className="cellDate">
                        {params.row.createdAt && moment(params.row.createdAt).format('llll')}
                    </div>
                }
            },
            {
                field: 'updatedAt',
                headerName: this.props.t('category.updatedAt'),
                type: "datetime",
                width: 200,
                renderCell: (params) => {
                    return <div className="cellDate">
                        {params.row.updatedAt && moment(params.row.updatedAt).format('llll')}
                    </div>
                }
            },
            {
                field: 'action',
                headerName: ' ',
                width: 180,
                renderCell: (params) => {
                    return <div className="cellAction">
                        <Tooltip
                            title={this.props.t('label.view')}
                        >
                            <Button class="btnAction view" onClick={() => this.viewCategory(params.row.id)}>
                                <CustomInlineSvg
                                    svgProps={{
                                        src: ViewIcon
                                    }}
                                />
                            </Button>
                        </Tooltip>
                        <Tooltip
                            title={this.props.t('label.edit')}
                        >
                            <Button class="btnAction edit" onClick={() => this.editCategory(params.row.id)}>
                                <CustomInlineSvg
                                    svgProps={{
                                        src: EditIcon
                                    }}
                                />
                            </Button>
                        </Tooltip>
                        <Tooltip
                            title={this.props.t('label.delete')}
                        >
                            <Button class="btnAction delete" onClick={() => this.deleteCategory(params.row.id)}>
                                <CustomInlineSvg
                                    svgProps={{
                                        src: DeleteIcon
                                    }}
                                />
                            </Button>
                        </Tooltip>
                    </div>
                }
            },
        ];

        const rows = dataCategories.map((dataCategory) => {
            return {
                id: dataCategory.id,
                name: dataCategory.name,
                description: dataCategory.description,
                photo: dataCategory.photo,
                createdAt: dataCategory.createdAt,
                updatedAt: dataCategory.updatedAt
            };
        });
        return (
            <div className={classes.categoryWrapper}>
                {isLoading && <LoadingAction/>}
                <ToastContainer
                    position="top-center"
                    autoClose={5000}
                    hideProgressBar={false}
                />
                {openModalNew && <CustomModal
                    open={openModalNew}
                    handleClose={() => this.closeModal("openModalNew")}
                    title={this.props.t('category.addCategory')}
                    hideSave={false}
                    hideCancel={true}
                    content={
                        <CategoryForm
                            formType={FORM_TYPE_NEW}
                            handleClose={() => {
                                this.getDataCategories();
                                this.closeModal("openModalNew")
                            }}
                        />
                    }
                    className={classes.modalNew}
                    hideFooter={true}
                />}
                {openModalEdit && <CustomModal
                    open={openModalEdit}
                    handleClose={() => this.closeModal("openModalEdit")}
                    title={this.props.t('category.updateCategory') + " - " + categoryId}
                    hideSave={false}
                    hideCancel={true}
                    content={
                        <CategoryForm
                            formType={FORM_TYPE_EDIT}
                            categoryId={categoryId}
                            handleClose={() => {
                                this.getDataCategories();
                                this.closeModal("openModalEdit")
                            }}
                        />
                    }
                    className={classes.modalNew}
                    hideFooter={true}
                />}
                {openModalView && <CustomModal
                    open={openModalView}
                    handleClose={() => this.closeModal("openModalView")}
                    title={this.props.t('category.viewCategory') + " - " + categoryId}
                    hideSave={false}
                    hideCancel={true}
                    content={
                        <CategoryView
                            categoryId={categoryId}
                            handleClose={() => {
                                // this.getDataCategories();
                                this.closeModal("openModalView")
                            }}
                        />
                    }
                    className={classes.modalNew}
                    hideFooter={true}
                />}
                {openModalDelete && <DialogForm
                    styleNumber={2}
                    dialogProps={{
                        open: this.state.openModalDelete
                    }}
                    disagreeButtonProps={{
                        handleDisagree: () => {
                            this.closeModal("openModalDelete");
                        }
                    }}
                    agreeButtonProps={{
                        handleAgree: () => {
                            this.closeModal("openModalDelete");
                            this.submitDeleteCategory(categoryId);
                        }
                    }}
                    messageProps={{
                        content: this.props.t('label.delete_question')
                    }}
                />}
                <div className={classes.categoryHeader}>
                    <div className="addCategory" onClick={() => this.openModal("openModalNew")}>
                        <CustomInlineSvg
                            svgProps={{
                                src: AddIcon
                            }}
                        />
                        <div className="text">
                            {this.props.t('category.addCategory')}
                        </div>
                    </div>
                </div>
                <div className={classes.categoryContent}>
                    <DataGrid
                        rows={rows}
                        columns={columns}
                        pageSize={5}
                    />
                </div>
            </div>
        );
    }
}

CategoryPage.propTypes = {
    classes: PropTypes.object.isRequired,
};


const mapStateToProps = (state) => {
    return {
        dataLogin: state.authReducer.dataLogin,
    }
};

const mapDispatchToProps = (dispatch)=>{
    return {
        setDataLogin: (dataLogin) => dispatch(authActions.setDataLogin(dataLogin)),
        setDataUser: (dataUser) => dispatch(authActions.setDataUser(dataUser)),
    }
};

export default compose(
    connect(mapStateToProps,mapDispatchToProps),
    withStyles(styles),
    withRouter,
    withTranslation()
)(CategoryPage);
