import React from "react";
import { withTranslation } from "react-i18next";
import i18n, {LANGUAGE_VI} from "../i18n";
import Select from "@material-ui/core/Select";
import MenuItem from "@material-ui/core/MenuItem";
import Cookies from 'js-cookie';
import moment from "moment";
import PropTypes from "prop-types";
import {compose} from "redux";
import {withStyles} from "@material-ui/core/styles";

const styles = {
    changeSelect: {
        '& div': {
            color: '#113990',
            fontSize: '1rem',
            fontWeight: 600
        },
        '& svg': {
            '& path': {
                fill: '#113990'
            }
        }
    },
    itemLanguage: {
        color: '#113990',
        fontWeight: 500,
        fontSize: '1rem'
    }
}

class ChangeLanguage extends React.Component{
    constructor(props) {
        super(props);
        this.state = {
            language: i18n.language
            // language: Cookies.get("_locale") ? Cookies.get("_locale") : LANGUAGE_VI
        };
        this.changeLanguage = this.changeLanguage.bind(this);
    }

    changeLanguage = (language) => {
        i18n.changeLanguage(language);
        Cookies.set('_locale',language, { expires: 30, path: "/" });
        moment.locale(language);
        this.setState({
            language: language,
            locale: language
        })
    };

    render() {
        const {
            classes
        } = this.props;
        return (
            <Select
                value={this.state.language}
                onChange={(event) => this.changeLanguage(event.target.value)}
                className={classes.changeSelect}
            >
                <MenuItem value={'vi'} className={classes.itemLanguage}>
                    {this.props.t('translations.vi')}
                </MenuItem>
                <MenuItem value={'en'} className={classes.itemLanguage}>
                    {this.props.t('translations.en')}
                </MenuItem>
            </Select>
        );
    }
}

ChangeLanguage.propTypes = {
    classes: PropTypes.object.isRequired,
};


export default compose(
    withStyles(styles),
    withTranslation()
)(ChangeLanguage);
