 import React from "react";
import {Button, Link, Typography} from "@material-ui/core";
import * as links from "../../constants/links";
import PropTypes from "prop-types";
import {compose} from "redux";
import {withStyles} from "@material-ui/core/styles";
import {NavLink, Redirect, withRouter} from "react-router-dom";
import {withTranslation} from "react-i18next";
const styles = {
    pageError: {
        width: '100%',
        height: '100vh',
        backgroundColor: 'rgba(39, 178, 126, 0.1)'
    },
    btnGoToHome: {
        background: '#113990',
        borderRadius: 20,
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        padding: '0.5rem 1.5rem',
        color: '#fff',
        margin: 'auto',
        fontSize: '1rem',
        fontWeight: 600,
        '&:hover': {
            background: '#113990',
            opacity: 0.9,
        }
    }
};

class PageError extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            redirectToHome: false
        };
    }


    render() {
        const {
            classes
        } = this.props;
        const {
            redirectToHome
        } = this.state;
        if (redirectToHome) {
            return (
                <Redirect to={links.LINK_HOME} />
            );
        }
        return (
            <div className={classes.pageError} style={{textAlign: 'center'}}>
                {this.props.code && <Typography variant="h1" style={{fontWeight: 700, marginBottom: '1rem', fontSize: '8rem'}}>{this.props.code}</Typography>}
                {this.props.title &&  <Typography variant="h2" style={{marginBottom: '1rem'}}>{this.props.title}</Typography>}
                {this.props.goToHome && <Typography variant="caption" style={{fontSize: '1rem'}}>
                    <Button className={classes.btnGoToHome} onClick={(e) => {
                        e.preventDefault();
                        if (typeof this.props.resetErrorBoundary === 'function') {
                            this.props.resetErrorBoundary();
                        }
                        this.setState({
                            redirectToHome: true
                        })
                    }}>{this.props.t("label.goToHome")}</Button>
                </Typography>}
            </div>
        );
    }
}

PageError.propTypes = {
    title: PropTypes.string.isRequired,
    classes: PropTypes.object.isRequired,
};
export default compose(
    withStyles(styles),
    withRouter,
    withTranslation()
)(PageError);