import React from "react";
import {compose} from "redux";
import {connect} from "react-redux";
import {withStyles} from "@material-ui/core/styles";
import {withTranslation} from "react-i18next";
import PropTypes from "prop-types";
import {withRouter} from 'react-router-dom';
import firebaseApp from "../../firebaseApp";
import LoadingAction from "../../theme/LoadingAction";
import {NUNITO} from "../../constants/fontCss";
import moment from "moment";
const styles = {
    categoryViewWrapper: {
        color: '#001529',
        '& .photoView': {
            height: 200,
            '& img': {
                height: 200,
            }
        },
        '& .nameView': {
            fontSize: '1rem',
            fontWeight: 700,
            color: '#001529',
            fontFamily: NUNITO,
            padding: '0.5rem 0'
        },
        '& .descriptionView': {
            fontSize: '0.875rem',
            fontWeight: 'normal',
            color: '#001529',
            fontFamily: NUNITO,
            padding: '0.5rem 0',
            whiteSpace: "pre-wrap",
            maxHeight: 150,
            overflowY: 'auto',
        },
        '& .dateView': {
            display: 'flex',
            alignItems: 'flex-start',
            justifyContent: 'center',
            padding: '1rem 0',
            '& .itemDate': {
                flexGrow: 1,
                '& .label': {
                    fontSize: '1rem',
                    fontWeight: 700,
                },
                '& .view': {
                    fontSize: '0.875rem',
                    fontWeight: 500,
                }
            },
        },
    }
};

class CategoryView extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            // dataCategory
            id: null,
            name: '',
            description: '',
            photo: null,
            createdAt: null,
            updatedAt: null,

            // state
            isLoading: false,
        };
    }

    componentDidMount() {
        const {
            categoryId
        } = this.props;
        this.setState({
            isLoading: true
        })
        this.showCategory(categoryId)
            .then(res => {
                const data = res.data();
                const id = res.id;
                let createdAt = null;
                let updatedAt = null;
                try {
                    createdAt = data.createdAt.toDate();
                    updatedAt = data.updatedAt.toDate();
                } catch (e) {

                }
                this.setState({
                    id: id,
                    name: data.name,
                    description: data.description,
                    photo: data.photo,
                    createdAt: createdAt,
                    updatedAt: updatedAt,
                })
            })
            .catch(err => {

            })
            .finally(() => {
                this.setState({
                    isLoading: false
                })
            })
    }
    async showCategory(categoryId) {
        const response = firebaseApp
            .firestore()
            .collection('categories')
            .doc(categoryId)
            .get();
        return await response;
    }

    render() {
        const {
            classes,
        } = this.props;
        const {
            name,
            description,
            photo,
            createdAt,
            updatedAt,
            isLoading,
        } = this.state;

        return (
            <div className={classes.categoryViewWrapper}>
                {isLoading && <LoadingAction/>}
                <div className="photoView">
                    {photo && <img src={photo} alt=""/>}
                </div>
                <div className="nameView">
                    {name}
                </div>
                <div className="descriptionView">
                    {description}
                </div>
                <div className="dateView">
                    <div className="itemDate">
                        <div className="label">
                            {this.props.t('category.createdAt')}
                        </div>
                        <div className="view">
                            {createdAt && moment(createdAt).format('llll')}
                        </div>
                    </div>
                    <div className="itemDate">
                        <div className="label">
                            {this.props.t('category.updatedAt')}
                        </div>
                        <div className="view">
                            {updatedAt && moment(updatedAt).format('llll')}
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

CategoryView.propTypes = {
    classes: PropTypes.object.isRequired,
};


const mapStateToProps = (state) => {
    return {
    }
};

const mapDispatchToProps = (dispatch)=>{
    return {

    }
};

export default compose(
    connect(mapStateToProps,mapDispatchToProps),
    withStyles(styles),
    withRouter,
    withTranslation()
)(CategoryView);
